<template>
    <div class="wrapper-content wrapper-content-inner has-minheight">
        <div class="row-b">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled :value="user.login"
                              name="login"
                              :placeholder="'Телефон'"
                              :disabled="true"
                              :is-block-arrow-phone="true"
                              :mask="{mask:'+7(999)999-99-99',showMaskOnHover: false}"
                              :isValidate="true"
                ></input-styled>
            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s"></div>
            </div>
        </div>
        <div class="row-b">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled v-model="lastName"
                              name="lastName"
                              :placeholder="'Фамилия'"
                              :disabled="formRegistrationFields.lastName.disabled"
                              :isValidate="formRegistrationFields.lastName.disabled"
                              :placeholder-input="'Иванович'"
                              @enter="enter(formRegistrationFields.lastName.name)"
                              :isError="formRegistrationFields.lastName.error"
                              :errorText="formRegistrationFields.lastName.errorText"
                              @focus="removeError(formRegistrationFields.lastName.name)"
                              @input="removeError(formRegistrationFields.lastName.name)"
                              @blur="saveField(formRegistrationFields.lastName.name)"
                ></input-styled>

            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s"></div>
            </div>
        </div>

        <div class="row-b">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled v-model="firstName"
                              name="firstName"
                              :placeholder="'Имя'"
                              :disabled="formRegistrationFields.firstName.disabled"
                              :isValidate="formRegistrationFields.firstName.disabled"
                              :placeholder-input="'Иван'"
                              @enter="enter(formRegistrationFields.firstName.name)"
                              :isError="formRegistrationFields.firstName.error"
                              :errorText="formRegistrationFields.firstName.errorText"
                              @focus="removeError(formRegistrationFields.firstName.name)"
                              @input="removeError(formRegistrationFields.firstName.name)"
                              @blur="saveField(formRegistrationFields.firstName.name)"
                ></input-styled>
            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s"></div>
            </div>
        </div>

        <div class="row-b">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled v-model="secondName"
                              name="secondName"
                              :placeholder="'Отчество'"
                              :disabled="formRegistrationFields.secondName.disabled"
                              :isValidate="formRegistrationFields.secondName.disabled"
                              :placeholder-input="'Иванов'"
                              @enter="enter(formRegistrationFields.secondName.name)"
                              :isError="formRegistrationFields.secondName.error"
                              :errorText="formRegistrationFields.secondName.errorText"
                              @focus="removeError(formRegistrationFields.secondName.name)"
                              @input="removeError(formRegistrationFields.secondName.name)"
                              @blur="saveField(formRegistrationFields.secondName.name)"
                ></input-styled>
            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s"></div>
            </div>
        </div>

        <div class="row-b">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled v-model="email"
                              name="pochta"
                              :placeholder="'E-mail'"
                              :disabled="formRegistrationFields.email.disabled"
                              :isEdit="formRegistrationFields.email.disabled"
                              @enter="enter(formRegistrationFields.email.name)"
                              @edit="editField(formRegistrationFields.email.name)"
                              :isError="formRegistrationFields.email.error"
                              :errorText="formRegistrationFields.email.errorText"
                              @focus="removeError(formRegistrationFields.email.name)"
                              @input="removeError(formRegistrationFields.email.name)"
                ></input-styled>
            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import InputStyled from '@/components/forms/InputStyled.vue'
    import { mapState } from 'vuex'

    export default {
        name: "UserUpdate",
        components: {
            InputStyled
        },
        computed: {
            firstName: {
                get () {
                    return this.formRegistrationFields.firstName.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.firstName.name,
                        value:value,
                    })
                }
            },
            lastName: {
                get () {
                    return this.formRegistrationFields.lastName.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.lastName.name,
                        value:value,
                    })
                }
            },
            secondName: {
                get () {
                    return this.formRegistrationFields.secondName.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.secondName.name,
                        value:value,
                    })
                }
            },
            email: {
                get () {
                    return this.formRegistrationFields.email.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.email.name,
                        value:value,
                    })
                }
            },
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                formRegistrationFields: state => state.auth.forms.registration.fields,
            }),
        },
        watch: {
            user(){
                this.initField();
            }
        },
        mounted() {
            this.initField();
        },
        methods: {
            editField (field) {
                this.$store.commit('auth/setFormRegistrationDisabled', {
                    name: field,
                    value:false,
                })
            },
            initField() {
                if(!!this.user.firstName && this.user.firstName.trim()){
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.firstName.name,
                        value:this.user.firstName.trim(),
                    });
                    this.$store.commit('auth/setFormRegistrationDisabled', {
                        name: this.formRegistrationFields.firstName.name,
                        value:true,
                    })
                }
                if(!!this.user.secondName && this.user.secondName.trim()){
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.secondName.name,
                        value:this.user.secondName.trim(),
                    });
                    this.$store.commit('auth/setFormRegistrationDisabled', {
                        name: this.formRegistrationFields.secondName.name,
                        value:true,
                    })
                }
                if(!!this.user.lastName && this.user.lastName.trim()){
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.lastName.name,
                        value:this.user.lastName.trim(),
                    });
                    this.$store.commit('auth/setFormRegistrationDisabled', {
                        name: this.formRegistrationFields.lastName.name,
                        value:true,
                    })
                }
                if(!!this.user.email && this.user.email.trim()){
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.email.name,
                        value:this.user.email.trim(),
                    });
                    this.$store.commit('auth/setFormRegistrationDisabled', {
                        name: this.formRegistrationFields.email.name,
                        value:true,
                    })
                }
            },
            enter (field) {
                this.removeError(field);
            },
            inputFocus(ref) {
                if(!!this.$refs[ref]){
                    this.$refs[ref].$el.getElementsByClassName('inp-s')[0].focus()
                }
            },
            saveField(field) {
                let self = this;
                if(this.formRegistrationFields[field].value.length > 2) {
                    setTimeout(function () {
                        self.$store.dispatch('users/save');
                    },2000);
                }
            },
            removeError (field) {
                this.$store.commit('auth/setFormRegistrationError',{ name: field, value: false })
            },
        }
    }
</script>

