<template>
    <section class="container">
        <popup :is-set-styled="true" :show="popups.couponCancel.show" :name="popups.couponCancel.name">
            <coupon-cancel :coupon="currentItem"></coupon-cancel>
        </popup>


        <popup :is-set-styled="true" :show="popups.couponClaim.show" :name="popups.couponClaim.name">
            <coupon-claim :action="currentItem.action"></coupon-claim>
        </popup>


        <popup :show="popups.repeatSendInfo.show" :name="popups.repeatSendInfo.name" :is-btn-close="false">
            <repeat-send-info></repeat-send-info>
        </popup>


        <popup :show="popups.checkoutFail.show" :name="popups.checkoutFail.name" :is-btn-close="false">
            <checkout-fail></checkout-fail>
        </popup>


        <popup :is-set-styled="true" :show="popups.couponHistory.show" :name="popups.couponHistory.name">
            <coupon-history :coupon="currentItem"></coupon-history>
        </popup>

        <popup :is-set-styled="true" :show="popups.couponInstruction.show" :name="popups.couponInstruction.name">
            <coupon-instruction :coupon="currentItem"></coupon-instruction>
        </popup>

        <div class="coupon-list mt0">
            <coupon-list-item v-for="coupon in coupons"
                               :item="coupon"
                              @current="setCurrent"
                               :key="coupon.id">
            </coupon-list-item>

            <div v-if="loadAction" class="coupon-stat">
                <div class="coupon-stat__wrap">
                    <catalog-ghost></catalog-ghost>
                </div>
                <div class="coupon-stat__control"></div>
            </div>
            
            <div v-else-if="isEmptyLoad" class="wrapper-content">
                <div class="sect-blank">
                    <div class="wrap text-center">
                        <div class="text-size-l text-bolder text-color-grey mt16">У вас пока нет оформленных купонов</div>
                        
                        <div class="mt16">
                            <router-link to="/catalog" class="link-icon" >
                                <span class="i i-book2 mt-2"></span>
                                <span class="text text-size-s text-bold">ПЕРЕЙТИ В КАТАЛОГ СКИДОК</span>
                            </router-link>
                        </div>
                        
                        <div class="mini-zems"></div>
                    </div>
                </div>
                <!-- end sect-blank -->
            </div>
        </div>

    </section>
    <!-- end coupon-list -->
</template>


<script>
    import { mapState } from 'vuex'
    import CouponListItem from '@/components/user/CouponListItem.vue'
    import Loader from '@/components/Loader.vue';
    import CouponCancel from '@/components/modal/CouponCancel.vue';
    import CouponClaim from '@/components/modal/CouponClaim.vue';
    import RepeatSendInfo from '@/components/modal/RepeatSendInfo.vue';
    import CheckoutFail from '@/components/modal/CheckoutFail.vue';
    import CouponHistory from '@/components/modal/CouponHistory.vue';
    import Popup from '@/components/modal/Popup.vue';
    import CouponInstruction from '@/components/modal/CouponInstruction.vue'

    import CatalogGhost from '@/components/catalog/CatalogGhost.vue'

    export default {
        name: "CouponList",
        components: {
            CouponListItem,
            Loader,
            Popup,
            CouponCancel,
            CouponClaim,
            RepeatSendInfo,
            CheckoutFail,
            CouponHistory,
            CouponInstruction,
            CatalogGhost
        },
        props: {
            favorites: {
                default: function () {
                    return false
                }
            }
        },
        created () {
            this.$store.dispatch('userCoupons/getCouponList', {
                reload: true
            })
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            isEmptyLoad () {
                return !this.loadAction && !(this.coupons.length > 0);
            },
            ...mapState({
                loadAction: state => state.userCoupons.isLoad,
                currentCity:  state => state.cities.current,
                coupons:  state => state.userCoupons.coupons,
                favoritesItems: state => state.actions.favorites.items,
                popups: state => state.popups.items,
            }),
        },
        data(){
            return {
                coords: [],
                currentItem: {},
            }
        },
        methods: {
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('userCoupons/getCouponList', {currentSection: this.currentSection})
                }
            },
            setCurrent(item) {
                this.currentItem = item;
            }
        }

    }
</script>