<template>
    <div class="coupon-stat">
        <div class="coupon-stat__wrap">
            <div class="coupon">
                <tooltip
                     :direction="'left-center-direction'"
                     :add-class-wrap="'coupon__bookmark-wrap'"
                     :add-style="{width: 248 + 'px', 'top': '67px', 'right': '126px'}">
                    <div slot="tooltipBtn" class="coupon__bookmark">
                        <div class="wrap">
                            <span class="d">−{{ item.action.discountFrom }}%</span>
                            <span class="p">
                                ≈{{ item.action.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                            </span>
                        </div>
                    </div>

                    <div slot="tooltipContent" style="font-size: 16px; ">
                        При обращении к этому<br> 
                        поставщику средний чек<br> 
                        составляет <strong>{{ item.action.averageCheckFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                        а экономия – около
                        <strong>{{ item.action.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                    </div>
                </tooltip>
                
                <div class="coupon__status-sum" v-if="item.status.code === 'extinguished'" >
                    <div class="t">Сумма покупки: <strong>{{ item.priceFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong></div>
                    <div class="t">Скидка: <strong>{{ item.discount.sumFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong> ({{ item.discount.percent }}%)</div>
                </div>
                
                <router-link @click.native="activeItem(item.action.id)"
                             :to="{ name: 'catalogDetail', params: { id: item.action.id }}"
                             class="coupon__link"></router-link>
                
                <div class="coupon__wrap">
                    <div class="coupon__img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.action.image+')' }"></div>

                    <div class="coupon__content">
                        <div class="coupon__favorite">
                            <span ref="favoriteBtn" @click="setFavorite(item)" class="check-favorites" :class="{active: item.action.favorite}"></span>
                        </div>

                        <div class="coupon__stat">
                            <span class="elem --views">
                                <span class="i i-eye"></span>
                                <span class="t">{{ item.action.showCounterFormat }}</span>
                            </span>

                            <span class="elem --buy" v-if="item.action.statistics.sold > 0">
                                <span class="i i-cart"></span>
                                <span class="t">{{ item.action.statistics.sold }}</span>
                            </span>

                            <!--<span class="elem &#45;&#45;comment" v-if=" item.action.countOpinions > 0">
                                <span class="i i-comment"></span>
                                <span class="t">{{  item.action.countOpinions }}</span>
                            </span>-->
                        </div>

                        <div class="coupon__name">
                            {{ item.action.name }}
                        </div>

                        <div class="coupon__company">{{ item.action.provider.name }}</div>


                        <div class="w">
                            <div class="coupon__economy">
                                <div class="t">
                                    Экономия

                                    <tooltip :direction="'bottom-right-direction'"
                                             :add-class-wrap="'dib mt-2'"
                                             :add-style="{width: 376 + 'px'}">
                                        <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>

                                        <div slot="tooltipContent">
                                            <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                            <ul class="ul-disc-small">
                                                <li>Средний чек у данного поставщика составляет {{ item.action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                                <li>Предоставляемая скидка составляет {{ item.action.discountFrom }}%</li>
                                                <li>{{ item.action.discountFrom }}% от {{ item.action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ item.action.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                            </ul>

                                            <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{ item.action.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                        </div>
                                    </tooltip>
                                </div>
                                <div class="p" >
                                    {{ item.action.realSavingsFormat }}
                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                </div>
                            </div>


                            <div class="coupon__info">
                                Средний чек
                                {{ item.action.averageCheckFormat }}
                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                                скидка
                                <span class="n">{{ item.action.discountFrom }}%</span>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end coupon__wrap -->
            </div>
            <!-- end coupon -->
        </div>
        <!-- end coupon-stat__wrap -->



        <div class="coupon-stat__control">
            <div class="stat-icon" v-if="item.status.code === 'valid'">Активен</div>
            <div class="stat-icon" v-if="item.status.code === 'extinguished'">Использован {{ item.status.date }}</div>
            <div class="stat-icon" v-if="item.status.code === 'expired'">Просрочен {{ item.status.date }}</div>
            <div class="stat-icon" v-if="item.status.code === 'canceled'">Аннулирован {{ item.status.date }}</div>
            <div class="stat-icon" v-if="item.status.code === 'withdrawn'">Отменён {{ item.status.date }}</div>


            <div class="link">
                <a href="javascript:void(0);" @click="openCouponHistory" class="link-icon">
                    <span class="w">
                        <span class="i i-info"></span>
                    </span>
                    <span class="t">Посмотреть историю</span>
                </a>
            </div>

            <div class="link" v-if="item.status.code === 'valid'">
                <a href="javascript:void(0);" @click="openRepeatSendInfo" class="link-icon">
                    <span class="w">
                        <span class="i i-send"></span>
                    </span>
                    <span class="t">Отправить на почту и по СМС</span>
                </a>
            </div>

            <div class="link" v-if="item.status.code === 'valid'">
                <a href="javascript:void(0);" @click="openCouponClaim" class="link-icon">
                    <span class="w">
                        <span class="i i-message"></span>
                    </span>
                    <span class="t">Пожаловаться на купон</span>
                </a>
            </div>

            <div class="link" v-if="item.status.code === 'valid'">
                <a href="javascript:void(0);" @click="openCouponCancel" class="link-icon">
                    <span class="w">
                        <span class="i i-close2"></span>
                    </span>
                    <span class="t">Отказаться от купона</span>
                </a>
            </div>

            <div class="link" v-if="item.status.code !== 'valid'">
                <!-- для примера по ссылке открывается окно с ошибкой, но должно быть оформление -->
                <router-link  @click.native="activeItem(item.action.id)"
                              :to="{ name: 'catalogDetail', params: { id: item.action.id }}"
                              class="link-icon"
                              v-if="+item.action.status === 19">
                    <span class="w">
                        <span class="i i-refresh"></span>
                    </span>
                    <span class="t">Оформить ещё раз</span>
                </router-link>
                <a href="javascript:void(0);" @click="openCheckoutFail" class="link-icon" v-else>
                    <span class="w">
                        <span class="i i-refresh"></span>
                    </span>
                    <span class="t">Оформить ещё раз</span>
                </a>
            </div>
            

            <div class="sep"></div>

            <div class="link">
                <a href="javascript:void(0);" @click="openCouponInstruction" class="link-icon">
                    <span class="w">
                        <span class="i i-info i--orange"></span>
                    </span>
                    <span class="t text-color-orange text-bold">Как воспользоваться скидкой?</span>
                </a>
            </div>
        </div>
        <!-- end coupon-stat__control -->
        


    </div>
    <!-- end coupon-stat -->
</template>

<script>
    import Popup from '@/components/modal/Popup.vue';
    import {flyToFavorite} from '@/helpers/animate';
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';

    export default {
        name: "CatalogListItem",
        components: {
            Popup,
            Tooltip,
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
            activeItem: function (id) {
                if(id){
                    this.$store.dispatch('actions/goCoupon', {id:id});
                }
            },
            setFavorite: function (item) {
                this.$store.dispatch('actions/setFavorite', item.action);
                if(item.action.favorite){
                    this.$store.commit('userCoupons/removeFavorite', item);
                } else {
                    this.$store.commit('userCoupons/addFavorite', item);
                }
                if(document.getElementById('h-favorites')){
                  flyToFavorite(this.$refs.favoriteBtn);
                }

            },
            openCouponCancel: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponCancel.name});
                this.$emit('current', this.item);
            },
            openCouponClaim: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponClaim.name});
                this.$emit('current', this.item);
            },
            openRepeatSendInfo: function () {
                this.$store.dispatch('userCoupons/resendCoupon', this.item);
                this.$store.dispatch('popups/open', {name: this.popups.repeatSendInfo.name});
                this.$emit('current', this.item);
            },
            openCheckoutFail: function () {
                this.$store.dispatch('popups/open', {name: this.popups.checkoutFail.name});
                this.$emit('current', this.item);
            },
            openCouponHistory: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponHistory.name});
                this.$emit('current', this.item);
            },
            openCouponInstruction: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponInstruction.name});
                this.$emit('current', this.item);
            },
        }
    }

</script>

<style scoped>

</style>
