            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
            }),
            countType(){
                let count = 0;
                if(this.coupon.callUsJson){
                    count++;
                }
                if(this.coupon.goLinkJson){
                    count++;
                }
                if(this.coupon.comeUsJson){
                    count++;
                }
                return count
            }
