<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap">
                <h1>Личные данные</h1>
            </div>

            <div class="main-title__right" v-if="token">
                <router-link to="/personal" tag="span" class="content-title__link --grey --icon ml32 hover">
                    <span class="i i-card2"></span>
                    <span class="text text-size-s">Мои купоны</span>
                </router-link>

                <span class="content-title__link --grey --icon ml32 hover" @click="logout">
                    <span class="i i-back"></span>
                    <span class="text text-size-s">ВЫЙТИ</span>
                </span>

            </div>
        </div>
        <section class="container">

            <div class="wrapper-content wrapper-content-inner has-minheight" v-if="!token">

                <div class="authorization authorization--empty">
                    <div class="wrap text-center">
                        <div class="title text-color-red">Вы не авторизованы</div>
                        <div class="mt24">
                            <a href="javascript:void(0);"  @click="showPopup(popups.publicAuth.name)">Войти</a>
                        </div>
                    </div>
                </div>
                <!-- end starter-authorization -->

            </div>
            <!-- end wrapper-content -->
            <user-update v-else></user-update>
        </section>
    </div>
</template>

<script>
    import UserUpdate from '@/components/user/auth/UserUpdate.vue'
    import { mapState } from 'vuex'
    export default {
        name: "Profile",
        components: {
            UserUpdate,
        },
        computed: {
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            ym(39511350, 'hit', this.$route.path, {title: 'Личные данные | ZemSkidki.ru'});
        },
        methods: {
            logout() {
                this.$store.dispatch('auth/logout')
            },
            showPopup(popup) {
                this.$store.dispatch('popups/open',{name:popup});
            },
        }
    }
</script>
